<template>
  <div class="recommend">
  	<scroll class="recommend-content" :data="discList" ref="scrollComponent">
	  <div>
	  	  <div v-if="recommend.length" class="slider-wrapper">
	  	  	<slider>
	  	  	  <div v-for="item in recommend">
	  	  	  	<a :href="item.linkUrl">
	  	  	  	  <img :src="item.picUrl" alt="" @load="loadImage" class="needsclick">
	  	  	  	</a>
	  	  	  </div>
	  	  	</slider>
	  	  </div>
	  	  <div class="recommend-list">
	  	  	<h1 class="list-title"></h1>
	  	  	<ul>
	            <li v-for="item in discList" class="item">
	              <div class="icon">
	                <img width="60" height="60" v-lazy="item.imgurl">
	              </div>
	              <div class="text">
	                <h2 class="name" v-html="item.creator.name"></h2>
	                <p class="desc" v-html="item.dissname"></p>
	              </div>
	            </li>
	  	  	</ul>
	  	  </div>
	  </div>
	  <div class="loading-container" v-if="!discList.length">
	  	<loading></loading>
	  </div>
  	</scroll>
  </div>
</template>

<script>
import slider from 'base/slider/slider';
import scroll from 'base/scroll/scroll';
import loading from 'base/loading/loading';
import { getRecommend,getDiscList } from 'api/recommend';
import { ERR_OK } from 'api/config';

export default {
  name: 'recommend',
  components: {
  	slider,
  	scroll,
  	loading
  },
  data() {
  	return {
  	  recommend: [],
  	  discList: []
  	}
  },
  created() {
  	this._getRecommend();
  	this._getDiscList();
  },
  methods: {
  	_getRecommend() {
  	  const self = this;
  	  getRecommend().then((res)=>{
  	  	console.log(res);
  	  	if(res.code === ERR_OK) {
  	  	  self.recommend = res.data.slider;
  	  	}
  	  });
  	},
  	_getDiscList() {
  	  const self = this;
  	  getDiscList().then((res)=> {
  	  	if(res.code === ERR_OK) {
  	  	  self.discList = res.data.list;
  	  	}
  	  });
  	},
  	loadImage() {
  	  if(!this.imageLoading) {
  	  	this.$refs.scrollComponent.refresh();
  	  	this.imageLoading = true;
  	  }
  	}
  }
}
</script>

<style lang="stylus">
  @import "~common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


